import { onMounted } from 'vue';
import * as THREE from 'three';
import { ReflectorRTT } from 'three/examples/jsm/objects/ReflectorRTT.js';
import { Refractor } from 'three/examples/jsm/objects/Refractor.js';
import { WaterRefractionShader } from 'three/examples/jsm/shaders/WaterRefractionShader.js';
import { PhongNodeMaterial, ReflectorNode, } from 'three/examples/jsm/nodes/Nodes.js';

export default function addBackground(scene: THREE.Scene, renderer: THREE.WebGLRenderer) {

    onMounted( () => {
        const textureLoader = new THREE.TextureLoader();

        const groundTexture = textureLoader.load('textures/grass.jpg');
        groundTexture.wrapS = groundTexture.wrapT = THREE.RepeatWrapping;
        groundTexture.repeat.set(250, 250);
        groundTexture.anisotropy = 3;
        groundTexture.encoding = THREE.sRGBEncoding;

        const groundMaterial = new THREE.MeshPhongMaterial({ map: groundTexture });

        const mesh = new THREE.Mesh(new THREE.PlaneBufferGeometry(5000, 5000), groundMaterial);
        mesh.position.y = -1;
        mesh.rotation.x = -Math.PI / 2;
        mesh.receiveShadow = true;
        scene.add(mesh);

        // Mapa otoczenia
        const pmremGenerator = new THREE.PMREMGenerator(renderer);
        new THREE.TextureLoader().load( 'img/noon_grass.jpg', (texture) => {
            texture.encoding = THREE.sRGBEncoding;
            scene.environment = pmremGenerator.fromEquirectangular(texture).texture;
            //scene.background = pmremGenerator.fromEquirectangular(texture).texture;
            texture.dispose();
        } );

        // Mapa tła
        new THREE.CubeTextureLoader().setPath('img/noon_grass_skybox/').load( [
            'noon_grass_left.jpg', 'noon_grass_right.jpg',
            'noon_grass_top.jpg', 'noon_grass_bottom.jpg',
            'noon_grass_back.jpg', 'noon_grass_front.jpg'
        ], (texture) => {
            scene.background = texture;
        } );

        // Sfera tła
        // const geometry = new THREE.SphereBufferGeometry( 800, 32, 32 );
        // const material = new THREE.MeshLambertMaterial({ map: new THREE.TextureLoader().load('img/noon_grass.jpg') });
        // material.side = THREE.BackSide;
        // const sphere = new THREE.Mesh(geometry, material);
        //scene.add(sphere);

        // Lustro
        // const geometrym = new THREE.PlaneBufferGeometry(10, 10);
        // const groundMirror = new ReflectorRTT(geometrym, { clipBias: 0.003, textureWidth: window.innerWidth, textureHeight: window.innerHeight });
        // const groundMirrorMaterial = new PhongNodeMaterial();
        // const mirror = new ReflectorNode(groundMirror);
        // groundMirrorMaterial.environment = mirror;
        // const planeGeo = new THREE.PlaneBufferGeometry( 10.1, 10.1 );
        // const mirrorMesh = new THREE.Mesh(planeGeo, groundMirrorMaterial);
        // groundMirror.add(mirrorMesh);
        // groundMirror.rotateX(-Math.PI/2);
        // scene.add(groundMirror);
    });
}