import { onMounted, onUnmounted, Ref, ref } from 'vue';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Mesh, Material } from 'three';

export default function addClickableMockup(
        loader: GLTFLoader,
        scene: THREE.Scene,
        renderer: THREE.WebGLRenderer,
        camera: THREE.PerspectiveCamera,
        currentCamera: Ref, selectable: Mesh[],
        annotationRef: Ref, mockupPath: string
    ) {

    let currentHover: number | null = null;
    const currentSelected = ref();

    const resetOpacity = () => {
        if (currentHover) {
            const index = selectable.findIndex((mesh: Mesh) => mesh.id === currentHover);
            if (currentHover != currentSelected.value) {
                (selectable[index].material as Material).opacity = 0;
            }
            currentHover = null;
        }
    }

    const mockupToClick = () => {
        if (currentHover != currentSelected.value) {
            currentSelected.value = currentHover;
        }
    }

    const mockupToRaycast = (raycaster: THREE.Raycaster) => {
        const intersects = raycaster.intersectObjects(selectable, true);
        if (intersects && intersects[0]) {
            resetOpacity();
            ((intersects[0].object as Mesh).material as Material).opacity = 0.5;
            ((intersects[0].object as Mesh).material as Material).transparent = true;
            currentHover = intersects[0].object.id;
        } else {
            resetOpacity();
            const index = selectable.findIndex((mesh: Mesh) => mesh.id === currentSelected.value);
            for (const mesh in selectable) {
                if (mesh != index.toString()) {
                    (selectable[mesh].material as Material).opacity = 0;
                }
            }
        }
    }

    const updateAnnotationPosition = () => {
        if (currentSelected.value) {
            const index = selectable.findIndex(mesh => mesh.id === currentSelected.value);
            const vector = selectable[index].position.clone();
            const canvas = renderer.domElement;

            vector.project(camera);

            vector.x = Math.round((0.5 + vector.x / 2) * (canvas.width / window.devicePixelRatio));
            vector.y = Math.round((0.5 - vector.y / 2) * (canvas.height / window.devicePixelRatio));
            annotationRef.value.style.top = `${vector.y}px`;
            annotationRef.value.style.left = `${vector.x}px`;
        }
    }

    const closeAnnotation = () => {
        currentSelected.value = null;
    }

    onMounted( () => {
        loader.load(
            mockupPath,
            gltf => {
                const model = gltf.scene;
                model.name = 'sciana';
                scene.add(model);
                for (const mesh in model.children) {
                    (model.children[mesh] as Mesh).material = new THREE.MeshStandardMaterial({ color: 0xff0000, opacity: 0, transparent: true });
                    selectable.push(model.children[mesh] as Mesh);
                }
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );
    })

    onUnmounted( () => {
        currentSelected.value = null; // Inaczej nie zatrzymuje updateAnnotationPosition()
    })

    return {
        currentSelected, mockupToRaycast,
        mockupToClick, updateAnnotationPosition,
        closeAnnotation
    };
}