import { onMounted, onUnmounted } from 'vue';
import * as THREE from 'three';


export default function mouseRaycaster(
        raycaster: THREE.Raycaster,
        camera: THREE.PerspectiveCamera,
    ) {

    let touchScreen = false;
    const mouse = new THREE.Vector2();
    const onCursorMoveFunctions: Array<Function> = [];

    const onCursorMove = (event: MouseEvent | TouchEvent) => {
        if (touchScreen) {
            mouse.x = ((event as TouchEvent).changedTouches[0].clientX / window.innerWidth) * 2 - 1;
            mouse.y = -((event as TouchEvent).changedTouches[0].clientY / window.innerHeight) * 2 + 1;
        } else {
            mouse.x = ((event as MouseEvent).clientX / window.innerWidth) * 2 - 1;
            mouse.y = -((event as MouseEvent).clientY / window.innerHeight) * 2 + 1;
        }

        raycaster.setFromCamera(mouse, camera);

        onCursorMoveFunctions.forEach(func => {
            func(raycaster);
        });
    }

    onMounted( () => {
        if ('ontouchstart' in document.documentElement) {
            touchScreen = true;
            window.addEventListener('touchend', onCursorMove, false);
        } else {
            window.addEventListener('mousemove', onCursorMove, false);
        }
    })

    onUnmounted ( () => {
        if (touchScreen) {
            window.addEventListener('touchend', onCursorMove, false);
        } else {
            window.addEventListener('mousemove', onCursorMove, false);
        }
    });

    return {
        onCursorMoveFunctions
    };
}