<template v-cloak>
    <div class="container">
        <div class="return">
            <!-- <router-link to="/flatscene">FLAT</router-link> -->
            <div class="change-camera" v-on:click="changeCamera(currentCamera, currentCamera === 'global' ? 'person' : 'global', 1500)">Change camera: {{ currentCamera }}</div>
        </div>

        <div class="preloader" v-show="loading.progress < 100">
            <div>
                Ładowanie
                <span v-show="loading.total">{{ loading.loaded }}/{{ loading.total }}</span>
            </div>
            <div v-show="loading.total">{{ loading.progress }}%</div>
        </div>

        <div class="scene" ref="sceneRef" v-on:click="sceneClick" v-on:touchstart="sceneClick"></div>

        <div class="annotation" ref="annotationRef" v-show="currentSelected">
            <div class="annotation-content">
                <div class="annotation-close" v-on:click="closeAnnotation()">x</div>
                <div class="annotation-title">Id mieszkania: {{ currentSelected }}</div>
                <div class="annotation-desc">
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam gravida massa odio, vitae accumsan mauris rutrum sed. Curabitur venenatis quam risus, sed rutrum magna cursus nec.
                    Fusce viverra ultricies nulla, et ultricies turpis. In eu mi eget velit sagittis tempus in non tortor.
                </div>
                <!-- <div class="annotation-button" v-on:click="clickCurrentSelected()">Zobacz mieszkanie</div> -->
            </div>
        </div>

    </div>
</template>

<script lang="ts">
import { ref } from 'vue'
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Mesh } from 'three';

// Import hooks
import createScene from '../hooks/createScene';
import addLights from '../hooks/addLights';
import addBackground from '../hooks/addBackground';
import postProduction from '../hooks/postProduction';
import addLoaderManager from '../hooks/addLoaderManager';
import addBuilding from '../hooks/addBuilding';
import mouseRaycaster from '../hooks/mouseRaycaster';
import addClickableMockup from '../hooks/addClickableMockup';
import addSprites from '../hooks/addSprites';

export default {
    setup() {
        // Dodaj scenę
        const sceneRef = ref(null);
        const {
            scene, renderer, camera, composer, currentCamera,
            changeCamera, control, animateFunctions,
            animation, sceneClick, sceneClickFunctions, camAndControls
        } = createScene(sceneRef);

        // Pokaż osie x,y,z
        //scene.add(new THREE.AxesHelper(100));

        // Dodaj światło
        addLights(scene)

        // Dodaj tło
        addBackground(scene, renderer)

        // Dodaj postprodukcję
        postProduction(composer, scene, camera, renderer)

        // Dodaj manager ładowania
        const manager = new THREE.LoadingManager();
        const { loading } = addLoaderManager(manager)

        // Załaduj budynek
        const loader = new GLTFLoader(manager);
        //addBuilding(loader, scene, 'develop_house/untitledDraco.gltf')
        addBuilding(loader, scene, 'lubawa/lubawa2.gltf')
        //addBuilding(loader, scene, 'monkey/monkey.gltf')
        //addBuilding(loader, scene, 'develop_house/modelDraco.gltf')

        // Funkcje raycastera
        const raycaster = new THREE.Raycaster();
        const { onCursorMoveFunctions } = mouseRaycaster(raycaster, camera)

        // Załaduj klikalny obiekt
        const selectable: Mesh[] = [];
        const annotationRef = ref(null)
        const {
            currentSelected, mockupToRaycast, mockupToClick,
            updateAnnotationPosition, closeAnnotation
        } = addClickableMockup(loader, scene, renderer, camera, currentCamera, selectable, annotationRef, 'develop_house/sciana.gltf')

        onCursorMoveFunctions.push(mockupToRaycast)
        sceneClickFunctions.push(mockupToClick)
        animateFunctions.push(updateAnnotationPosition)

        // Dodaj klikalne punkty
        const { spritesToRaycast, spritesToClick } = addSprites(scene, camera, control, animation)
        onCursorMoveFunctions.push(spritesToRaycast)
        sceneClickFunctions.push(spritesToClick)

        return {
            sceneRef, annotationRef, changeCamera,
            currentCamera, closeAnnotation, currentSelected,
            sceneClick, loading, camAndControls
        }
    }
}
</script>


<style scoped lang="scss">
.scene {
    width: 100vw;
    height: 100vh;
}

.info {
    z-index: 999;
    background-color: white;
    border: 1px solid black;
    position: absolute;
    right: 0;
    top: 0;
    text-align: left;
}

.container {
    position: relative;
}

.return {
    z-index: 999;
    background-color: white;
    border: 1px solid black;
    position: absolute;
    right: 0;
    top: 0;
    text-align: left;
    user-select: none;
}

.preloader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 998;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
    color: white;
}

.blur {
    filter: blur(20px);
}

.annotation {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    padding: 1em;
    width: 200px;
    color: #fff;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 0.5em;
    font-size: 12px;
    line-height: 1.2;
    transition: opacity 0.5s;

    &-content {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
    }

    &-close {
        position: absolute;
        top: -5px;
        right: 0;
        cursor: pointer;

        &:hover {
            font-weight: bold;
        }
    }

    &-title {
        font-weight: bold;
        margin-bottom: 20px;
    }

    &-desc {
        margin-bottom: 20px;
    }

    &-button {
        background-color: #00339a;
        padding: 5px 10px;
        cursor: pointer;
        border-radius: 10px;
        width: fit-content;
        align-self: center;
        opacity: 0.7;

        &:hover {
            opacity: 1;
        }
    }
}
</style>