import { onMounted, Ref } from 'vue';
import * as THREE from 'three';
import anime from 'animejs';
import { Mesh, Material, Vector3 } from 'three';


export default function addSprites(
        scene: THREE.Scene,
        camera: THREE.PerspectiveCamera,
        control: any,
        animation: Ref
    ) {

    let currentSelectedSprite: THREE.Intersection | null = null;

    const sprites: { id: number; position: THREE.Vector3 }[] = [
        {
            id: 1,
            position: new THREE.Vector3(50, 18, -12),
        },
        {
            id: 2,
            position: new THREE.Vector3(8, 20, -14),
        },
        {
            id: 3,
            position: new THREE.Vector3(10, 15, -8),
        },
        {
            id: 4,
            position: new THREE.Vector3(10, 5, -6),
        },
    ];
    const addedSprites: THREE.Sprite[] = [];

    const drawNumber = (number: string) => {
        const canvas = document.createElement('canvas');
        canvas.width = 64;
        canvas.height = 64;

        const x = 32;
        const y = 32;
        const radius = 30;
        const startAngle = 0;
        const endAngle = Math.PI * 2;

        const ctx = canvas.getContext('2d');
        if (ctx != null) {
            ctx.fillStyle = 'rgb(0, 0, 0)';
            ctx.beginPath();
            ctx.arc(x, y, radius, startAngle, endAngle);
            ctx.fill();

            ctx.strokeStyle = 'rgb(255, 255, 255)';
            ctx.lineWidth = 3;
            ctx.beginPath();
            ctx.arc(x, y, radius, startAngle, endAngle);
            ctx.stroke();

            ctx.fillStyle = 'rgb(255, 255, 255)';
            ctx.font = '32px sans-serif';
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.fillText(number, x, y);
        }
        return new THREE.CanvasTexture(canvas);
    }

    const addSprite = (number: string, position: THREE.Vector3) => {
        const numberTexture = drawNumber(number);

        const spriteMaterialFront = new THREE.SpriteMaterial({
            map: numberTexture,
        });

        const spriteMaterialBack = new THREE.SpriteMaterial({
            map: numberTexture,
            transparent: true,
            depthTest: false,
            opacity: 0.3,
        });

        const spriteFront = new THREE.Sprite(spriteMaterialFront);
        const spriteBack = new THREE.Sprite(spriteMaterialBack);

        spriteFront.position.copy(position);
        spriteBack.position.copy(position);

        spriteFront.userData.frontID = spriteFront.id;
        spriteFront.userData.backID = spriteBack.id;
        spriteBack.userData.frontID = spriteFront.id;
        spriteBack.userData.backID = spriteBack.id;

        addedSprites.push(spriteFront, spriteBack);
        scene.add(spriteFront, spriteBack);
    }

    const addAllSprites = () => {
        for (const key in sprites) {
            addSprite(sprites[key].id.toString(), sprites[key].position);
        }
    }

    const spritesToRaycast = (raycaster: THREE.Raycaster) => {
        const spritesIntersects = raycaster.intersectObjects(addedSprites, true);
        if (spritesIntersects && spritesIntersects[0]) {
            ((spritesIntersects[0].object as Mesh).material as Material).opacity = 0.1;
            currentSelectedSprite = spritesIntersects[0];
        } else {
            if (currentSelectedSprite) {
                ((currentSelectedSprite.object as Mesh).material as Material).opacity = 0.7;
                currentSelectedSprite = null;
            }
        }
    }

    const moveCameraToPosition = (newPosition: Vector3, duration: number, oncomplete?: Function) => {
        const currentAngle = control.getAzimuthalAngle();
        control.maxAzimuthAngle = currentAngle;
        control.minAzimuthAngle = currentAngle;
        control.enableRotate = false;

        const distance = camera.position.distanceTo(newPosition)
        const offset = 1

        const newPositionX = newPosition.clone()
        const newPositionY = newPosition.clone()
        const newPositionZ = newPosition.clone()

        newPositionX.x -= offset
        newPositionY.y -= offset
        newPositionZ.z -= offset

        const distanceX = camera.position.distanceTo(newPositionX)
        const distanceY = camera.position.distanceTo(newPositionY)
        const distanceZ = camera.position.distanceTo(newPositionZ)

        const newX = distanceX > distance ? newPosition.x + offset : newPosition.x - offset
        const newY = distanceY > distance ? newPosition.y + offset : newPosition.y - offset
        const newZ = distanceZ > distance ? newPosition.z + offset : newPosition.z - offset

        animation.value = anime({
            targets: camera.position,
            easing: 'easeOutCirc',
            duration: duration,
            complete: () => {
                animation.value = null;
            },
            x: newX,
            y: newY,
            z: newZ
        });

        animation.value = anime({
            targets: control.target,
            easing: 'easeOutCirc',
            duration: duration + 10,
            complete: () => {
                animation.value = null;
                control.maxAzimuthAngle = Infinity;
                control.minAzimuthAngle = -Infinity;
                control.enableRotate = true;
                if (oncomplete != undefined) {
                    oncomplete();
                }
            },
            x: newPosition.x,
            y: newPosition.y,
            z: newPosition.z,
        });
    }

    const spritesToClick = () => {
        if (currentSelectedSprite) {
            moveCameraToPosition(currentSelectedSprite.object.position, 1500);
        }
    }

    onMounted( () => {
        addAllSprites();
    });

    return {
        spritesToRaycast, spritesToClick
    };
}