import { onMounted, onUnmounted, Ref, ref} from 'vue';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import anime from 'animejs';

export default function createScene(sceneRef: Ref) {
    const scene: THREE.Scene = new THREE.Scene();
    const renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.shadowMap.enabled = true;
    renderer.shadowMap.type = THREE.VSMShadowMap;

    const renderTarget = new THREE.WebGLMultisampleRenderTarget(window.innerWidth, window.innerHeight);
    const composer: EffectComposer = new EffectComposer(renderer, renderTarget);

    const currentCamera = ref('global');
    const camera = new THREE.PerspectiveCamera()

    const camAndControls: any = {
        global: {
            camera: {
                fov: 60,
                aspect: sceneRef.value ? sceneRef.value.clientWidth / sceneRef.value.clientHeight : window.innerWidth / window.innerHeight,
                near: 1,
                far: 1000,
                position: new THREE.Vector3(-15, 2.5, 35),
            },
            controls: {
                enableDamping: true,
                dampingFactor: 0.05,
                screenSpacePanning: false,
                minDistance: 10,
                maxDistance: 5000,
                maxPolarAngle: Math.PI / 2,
                enablePan: true,
                position0: new THREE.Vector3(0, 0, 0),
                target: new THREE.Vector3(0, 0, 0),
                enableKeys: false,
                keyPanSpeed: 7,
                enableZoom: true,
            }
        },
        person: {
            camera: {
                fov: 60,
                aspect: sceneRef.value ? sceneRef.value.clientWidth / sceneRef.value.clientHeight : window.innerWidth / window.innerHeight,
                near: 1,
                far: 1000,
                position: new THREE.Vector3(76, 1, 76),
            },
            controls: {
                enableDamping: true,
                dampingFactor: 0.05,
                screenSpacePanning: false,
                minDistance: 10,
                maxDistance: 5000,
                maxPolarAngle: Math.PI / 2,
                enablePan: true,
                position0: new THREE.Vector3(0, 0, 0),
                target: new THREE.Vector3(75, 1, 75),
                enableKeys: true,
                keyPanSpeed: 70,
                enableZoom: false,
            }
        },
    }

    const control = new OrbitControls(camera, renderer.domElement)

    const animation = ref();
    const animateFunctions: Array<Function> = [];
    const sceneClickFunctions: Array<Function> = [];
    const clock = new THREE.Clock();

    const onWindowResize = () => {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
    }

    const sceneClick = () => {
        sceneClickFunctions.forEach(func => {
            func();
        });
    }

    const setCamControlsSettings = (camAndControls: any, camera: THREE.PerspectiveCamera, control: OrbitControls, currentCamera: Ref) => {
        const cameraSettings = camAndControls[currentCamera.value].camera
        Object.keys(cameraSettings).forEach(key => {
            if (key != 'position') {
                (camera as any)[key] = cameraSettings[key]
            } else {
                camera[key].set(cameraSettings[key].x, cameraSettings[key].y, cameraSettings[key].z)
            }
        })

        const controlsSettings = camAndControls[currentCamera.value].controls
        Object.keys(controlsSettings).forEach(key => {
            (control as any)[key] = controlsSettings[key]
        })
    }

    const changeCamera = (startCamera: string, endCamera: string, duration: number) => {
        control.enableRotate = false;
        
        animation.value = anime({
            targets: camera.position,
            easing: 'easeOutCirc',
            duration: duration,
            complete: () => {
                animation.value = null;
            },
            x: camAndControls[endCamera].camera.position.x,
            y: camAndControls[endCamera].camera.position.y,
            z: camAndControls[endCamera].camera.position.z,
        });

        const saveStartTarget = {...camAndControls[startCamera].controls.target}
        animation.value = anime({
            targets: control.target,
            easing: 'easeOutCirc',
            duration: duration * 1.1,
            complete: () => {
                animation.value = null;
                control.enableRotate = true;
                currentCamera.value = endCamera;
                camAndControls[startCamera].controls.target = saveStartTarget;
                camera.updateProjectionMatrix();
            },
            x: camAndControls[endCamera].controls.target.x,
            y: camAndControls[endCamera].controls.target.y,
            z: camAndControls[endCamera].controls.target.z,
        });
    }

    onMounted( () => {
        console.log(camera)
        // Ustawienia kamer i sterów kamery
        setCamControlsSettings(camAndControls, camera, control, currentCamera)

        camera.updateProjectionMatrix();
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(sceneRef.value.clientWidth, sceneRef.value.clientHeight);
        sceneRef.value.appendChild(renderer.domElement);

        // zmiana rozmiaru sceny
        window.addEventListener('resize', onWindowResize);

        // Główna pętla sceny
        const animate = () => {
            requestAnimationFrame(animate);
            (control as any).update(clock.getDelta());

            animateFunctions.forEach(func => {
                func();
            });

            // Funkcja renderująca
            //renderer.render(scene, camera);
            composer.render()
        };

        animate();
    });

    onUnmounted ( () => {
        renderer.forceContextLoss();
    });

    return {
        scene, renderer, camera, composer,
        currentCamera, changeCamera, control,
        animateFunctions, animation,
        sceneClick, sceneClickFunctions,
        camAndControls
    };
}