import { onMounted } from 'vue';
import * as THREE from 'three';


export default function addLights(scene: THREE.Scene) {
    onMounted( () => {
        const ambientLight = new THREE.HemisphereLight(0xffffff, 0x222222, 0.5);
        ambientLight.position.set(10, 10, 10);

        const mainLight = new THREE.DirectionalLight(0xffffff, 1);
        mainLight.shadow.camera = new THREE.OrthographicCamera( -75, 75, 75, -75, 1, 60)
        mainLight.position.set(5, 25, 5);
        mainLight.castShadow = true;
        mainLight.shadow.bias = -0.0001
        mainLight.shadow.mapSize.width = 2048; // default
        mainLight.shadow.mapSize.height = 2048; // default
        mainLight.shadow.camera.near = 1; // default
        mainLight.shadow.camera.far = 300; // default

        //const helper = new THREE.CameraHelper( mainLight.shadow.camera );
        scene.add(ambientLight, mainLight);
    })

    return {
        
    };
}